import './preloader.css';
import htmlText from './preloader.html';
/**
 * Preloader - добавляет экран загрузки
 */
class Preloader {
    constructor() {}
    /**
     * Загружает структуру прелоадера в страницу
     * @param {HTMLElement} insertNode - куда вставить прелоадер
     */
    start(insertNode) {
        const parser = new DOMParser();
        const parsed = parser.parseFromString(htmlText, `text/html`);
        let html = parsed.querySelector('.preloader');
        insertNode.append(html);
    }
    /**
     * Отображает / скрывает прелоадер
     * @param {boolean} state 
     */
    switch(state) {
        if (state === false) {
            let preloaderNode = document.querySelector('.preloader')
            preloaderNode.style.display = 'none';
        } else if (state === true) {
            let preloaderNode = document.querySelector('.preloader')
            preloaderNode.style.display = 'flex';
        }
    }

}

export default Preloader